@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&display=swap');

body {
  font-family: 'Oswald', sans-serif;
  overflow: hidden;
}

.nui-wrapper {
  position: absolute;
  left: 50%;
  bottom: 32px;
  transform: translate(-50%, -50%);
  margin: 0 auto;
  transition: all 1s;
  animation: SlideUp 0.3s;
  animation-fill-mode: forwards;
  overflow: hidden;
}

.nui-wrapper.false.blackbars-true {
  animation: SlideDown-Bars 0.3s;
  animation-fill-mode: forwards;
  overflow: hidden;
}

.nui-wrapper.true.blackbars-true {
  bottom: 120px;
  animation: SlideUp-Bars 0.3s;
}

.nui-wrapper.false {
  animation: SlideDown 0.3s;
  animation-fill-mode: forwards;
  overflow: hidden;
}

.progress-outside {
  padding: 16px;
  background: radial-gradient(50% 50% at 50% 50%, rgba(43, 57, 98, 0.4) 0%, rgba(36, 39, 63, 0.7) 100%);
  border: 1px solid rgba(0, 0, 0, 0.15);
  box-sizing: border-box;
  border-radius: 3px;
}

.progress-bar {
  display: flex;
  width: 349px;
  height: 30px;

  background: rgba(255, 255, 255, 0.16);
  border-radius: 4px;

  align-items: center;
  transition: all 1s;
}

.progress-circle {



  position: absolute;

  bottom: 0px;
  margin-bottom: 19px;
  margin-left: 15px;
  transition: all 0.1s;

  left: 1%;

}

.progress-inside {
  position: absolute;
  bottom: 0px;

  transition: all 0.05s;
  margin-bottom: 1.5px;
  margin-left: 1px;
  margin-right: 1px;

  font-weight: 500;
  font-size: 20px;
  line-height: 30px;  
  color: #FFFFFF;
  text-shadow: 0px 0px 4px #000000, 0px 0px 2px #000000;

}

.progress-bar > div {
  flex: 1;
}

.targetPoint {
  z-index: 1000;

  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;

  text-shadow: 0px 0px 4px #000000, 0px 0px 2px #000000;
  
  display: flex;
  align-items: center;

  letter-spacing: 0.205em;

  color: #FFFFFF;

  background: rgba(255, 255, 255, 0.1);

  text-align: center;

  justify-content: center;
  align-content: center;
}


@keyframes SlideDown {
  0% {
    bottom: 32px;
  }
  100% {
    bottom: -100px;
  }
}

@keyframes SlideUp {
  0% {
    bottom: -100px;
  }
  100% {
    bottom: 32px;
  }
}

@keyframes SlideDown-Bars {
  0% {
    bottom: 120px;
  }
  100% {
    bottom: -100px;
  }
}

@keyframes SlideUp-Bars {
  0% {
    bottom: -100px;
  }
  100% {
    bottom: 120px;
  }
}